import { useHistory } from 'react-router';
import '../../css/form-pendaftaran/formPendaftaran.css';

const FormPendaftaran = () => {
    const history = useHistory();
    
    return (<>
        <div className="formulirPendaftaran">
            <h4 className="judul mb-5">FORM REGISTRASI UKM</h4>
            <form className="form-input">
                <div className="form">
                    <div className="d-flex mx-5 my-3">
                        <label className="form-label col-4">Email</label>
                        <input type="text" className="form-control fillForm"></input>
                    </div>
                    <div className="d-flex mx-5 my-3">
                        <label className="form-label col-4">Nama Lengkap</label>
                        <input type="email" className="form-control fillForm"></input>
                    </div>
                    <div className="d-flex mx-5 my-3">
                        <label className="form-label col-4">Kelas</label>
                        <input type="text" className="form-control fillForm"></input>
                    </div>
                    <div className="d-flex mx-5 my-3">
                        <label className="form-label col-4">Jurusan</label>
                        <input type="jurusan" className="form-control fillForm"></input>
                    </div>
                    <div className="d-flex mx-5 my-3">
                        <label className="form-label col-4">Alamat</label>
                        <input type="alamat" className="form-control fillForm"></input>
                    </div>
                    <div className="d-flex mx-5 my-3">
                        <label className="form-label col-4">Nomor HP</label>
                        <input type="nohp" className="form-control fillForm"></input>
                    </div>
                    <div className="d-flex mx-5 my-3">
                        <label className="form-label col-4">Alasan Mengikuti UKM</label>
                        <textarea type="reason" className="form-control fillForm"></textarea>
                    </div>
                </div>
                <div className="d-flex justify-content-between mx-5 mt-5">
                    <button className="btn btn-kembali" onClick={(e) => { e.preventDefault(); history.goBack(); } }>KEMBALI</button>
                    <button className="btn btn-app">DAFTAR</button>
                </div>
            </form>
        </div>
    </>);
}

export default FormPendaftaran;