import '../../css/detail-ukm/GaleriUKM.css'
import { Carousel } from 'react-bootstrap';
import { useState } from 'react';

const GaleriUKM = () => {
    const imagesPath = '/images/detail-ukm/';
    const [index, setIndex] = useState(0);

    const handleSelect = (selectedIndex, e) => {
      setIndex(selectedIndex);
    };

    return (<>
        <Carousel activeIndex={index} onSelect={handleSelect}>
            <Carousel.Item>
                <img
                    className="d-block ukm-image" 
                    src={`${imagesPath}paduan-suara.png`}
                    alt="First slide"
                />
            </Carousel.Item>
            <Carousel.Item>
                <img
                    className="d-block ukm-image"
                    src={`${imagesPath}paduan-suara-2.png`}
                    alt="Second slide"
                />
            </Carousel.Item>
            <Carousel.Item>
                <img
                    className="d-block ukm-image"
                    src={`${imagesPath}paduan-suara-3.png`}
                    alt="Third slide"
                />
            </Carousel.Item>
        </Carousel>
    </>);
}

export default GaleriUKM;