import '../css/kategori-ukm/KategoriUKM.css';

import Nalar from '../components/kategori-ukm/Nalar.js';
import Olahraga from '../components/kategori-ukm/Olahraga.js';
import Seni from '../components/kategori-ukm/Seni.js';
import { useEffect } from 'react';
import { useParams } from 'react-router';

const PenalaranUKM = () => {
    const { id, name } = useParams();

    const getKategoriComponent = () => {
        const namaKategori = name.toLowerCase();
        if(namaKategori === 'olahraga')
            return <Olahraga />
        if(namaKategori === 'seni')
            return <Seni />
        if(namaKategori === 'penalaran')
            return <Nalar />
    }

    return(<>
        <div className="content-margin-nalar">  
            { getKategoriComponent() }
        </div>
    </>);
}

export default PenalaranUKM;