import { useEffect, useState } from 'react';
import '../../css/landing/KategoriLanding.css';

const KategoriLanding = () => {
  const imagesPath = '/images/kategori-landing/';

  const [kategoriUKM, setKategoriUKM] = useState([]);

  useEffect(() => {
    fetch(`http://103.30.145.87/api/v1/categories?token=${process.env.REACT_APP_API_TOKEN}`)
    .then(res => res.json())
    .then(resJson => setKategoriUKM(resJson.data));
  }, []);

  const getKategoriImg = (kategori) => {
    const namaKategori = (kategori.name).toLowerCase();
    let path;
    switch(namaKategori) {
        case 'olahraga': path = `${imagesPath}sport1.svg`;
        break;
        case 'penalaran': path = `${imagesPath}idea1.svg`;
        break;
        case 'seni': path = `${imagesPath}art1.svg`;
        break;
        default: path = `${imagesPath}idea1.svg`;
    }

    return path;
  }

  return (<>

  <div>
    <div class="container-tiga ">
        { 
            kategoriUKM.map((kategori, index) => {
                return(
                    <a href={`/kategori/${kategori.id}/${kategori.name}`}>
                        <div class="kategoriy" key={index}>
                            <h5 className="title-kategori text-center">{ (kategori.name).toUpperCase() }</h5>
                            <div class="card kategori-card">
                                <img src={getKategoriImg(kategori)} class="card-img-top" alt="icon" />
                            </div>
                        </div>
                    </a>
                );
            })
        }
    </div>
</div>

 </> );
}

export default KategoriLanding;
