import '../../css/layouts/NavigationBar.css';
import { Nav, Navbar, NavDropdown, Form, FormControl, Button } from 'react-bootstrap';
import { useEffect, useState } from 'react';

const NavigationBar = (props) => {
  const [ menuKategori, setMenuKategori ] = useState();

  useEffect (() => {
    fetch ("http://103.30.145.87/api/v1/categories?token=$2y$10$5we5Z7yY.qCxKBcDF91Wv.2W1XBaFmfAUhTQKxSsgk6kWSgIiVigW")
    .then (response => response.json())
    .then (menuKategori => {setMenuKategori(menuKategori)})
  }, []);

  return (<>
    <div className="navbarClass">
      <Navbar bg="navbar" sticky="top" expand="lg" collapseOnSelect>
        <div className="leftSide">
          <Nav className="leftMenuNavbar">
            <Nav.Link href="/"><img src="/images/logo/LOGO.png" width='180 px' height='60px' alt="logo" /></Nav.Link>
          </Nav>
        </div>
          <Navbar.Toggle />
          <Navbar.Collapse>
              <Form className="searchBar" >
                <FormControl className="searchInput" type="text" placeholder="Cari UKM" className="mr-sm-2" />
                <Button className="searchButton">Cari</Button>
              </Form>
              <div className="rightSide d-lg-flex">
                <Nav className="menuNavbar">
                  <NavDropdown title="Kategori UKM" class="menuDropdown">
                    {menuKategori?.data.map((data, i) => {
                      return(<NavDropdown.Item href= {`/kategori/${data.id}/${data.name}`} >{ data.name }</NavDropdown.Item>)
                    })
                    }
                  </NavDropdown>
                  <Nav.Link href="#">Saran UKM</Nav.Link>
                  <Nav.Link onClick={props.scrollToFooter}>Tentang Kami</Nav.Link>
                </Nav>
              </div>
          </Navbar.Collapse>
      </Navbar>
    </div>
  </>);
}
export default NavigationBar;